
@charset "UTF-8";
/***
**** base css ****
***/
.clearfix:after {
  zoom: 1;
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
html {
  overflow-x: hidden;
  overflow-y: auto;
}
body {
  font-size: 16px;
  background-color: #fff;
  font-family: "\5FAE\8F6F\96C5\9ED1";
  margin: 0;
  padding: 0;
  background: url(/static/img/bg.14d9007.jpg) top center;
}
a {
  text-decoration: none;
}
a:hover {
    text-decoration: none;
    color: #02B2B5;
}
li {
  list-style: none;
}
#app {
  text-align: center;
  color: #2c3e50;
}
#nav a {
  font-weight: bold;
  color: #2c3e50;
}
#nav a.router-link-exact-active {
    color: #42b983;
}
/***
**** 登录注册页面样式****
***/
.form .title {
  font-size: 32px;
  font-weight: bold;
  color: #2c4156;
  margin-bottom: 14px;
  padding-left: 42px;
}
.form .inputInfo {
  padding: 10px;
}
.form .inputInfo span {
    display: inline-block;
    width: 84px;
}
.form .inputInfo input {
    text-indent: 6px;
    outline: none;
    border-radius: 4px;
}
.form .btn {
  margin: 10px 0 0 84px;
}
.form .btn button {
    margin: 5px 10px;
    width: 76px;
    height: 28px;
    background-color: #6fc2e2;
    border: none;
    border-radius: 2px;
    color: #fff;
}
/***
**** 记事本页面样式****
***/
.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 62px;
  padding: 16px 100px;
  line-height: 30px;
  background-color: rgba(186, 203, 224, 0.7);
  -webkit-box-shadow: 2px -1px 2px #151313;
          box-shadow: 2px -1px 2px #151313;
}
.header .left {
    float: left;
    font-size: 28px;
    font-weight: bold;
}
.header .right {
    float: right;
}
.header .right li {
      display: inline-block;
      padding: 0 14px;
}
.addNote .title {
  font-size: 32px;
  font-weight: bold;
  color: #2c4156;
  margin-bottom: 18px;
}
.addNote .addTitle {
  margin-bottom: 16px;
}
.addNote .addTitle input {
    outline: none;
    text-indent: 8px;
}
.addNote .addCont textarea {
  outline: none;
  padding: 6px;
}
.addNote .btn {
  margin: 10px 0 0 84px;
}
.addNote .btn button {
    margin: 5px 10px;
    width: 96px;
    height: 38px;
    background-color: #6fc2e2;
    border: none;
    border-radius: 2px;
    color: #fff;
}
.user {
  padding-top: 100px;
}
.user .newNote {
    margin: 30px auto;
    padding-top: 100px;
    letter-spacing: 8px;
    border-radius: 50%;
    width: 250px;
    height: 250px;
    background: #F4D03F;
    -webkit-animation: myfirst 10s infinite alternate;
    animation: myfirst 10s infinite alternate;
}
.user .newNote a {
      font-size: 34px;
}
@keyframes myfirst {
0% {
    background: #F4D03F;
}
25% {
    background: #e6490d;
}
50% {
    background: #43A4CE;
}
80% {
    background: #30B557;
}
}
@-webkit-keyframes myfirst {
0% {
    background: #F4D03F;
}
25% {
    background: #e6490d;
}
50% {
    background: #43A4CE;
}
80% {
    background: #30B557;
}
}
.itemList ul {
  margin-top: 34px;
}
.itemList ul li {
    width: 94%;
    padding-left: 50px;
    padding-top: 28px;
}
.itemList ul li .time {
      float: left;
      width: 8%;
      height: 84px;
      color: #666666;
      background-color: #f2f2f2;
      text-align: center;
      padding-top: 30px;
      font-weight: bold;
}
.itemList ul li .noteCont {
      float: right;
      width: 90%;
}
.itemList ul li .noteCont .title .noteTitle {
        font-weight: bold;
}
.itemList ul li .noteCont .title .detail {
        color: #1d07d0;
        padding-right: 30px;
}
.itemList ul li .noteCont .title .detail:hover {
          color: #2c4156;
          cursor: pointer;
}
.itemList ul li .noteCont .title .delete {
        color: #1d07d0;
}
.itemList ul li .noteCont .title .delete:hover {
          color: #2c4156;
          cursor: pointer;
}
.itemList ul li .noteCont .cont {
        text-align: left;
}













































































































































































































